// 
import Api from '../../constants/Api'
// 
export default {
  namespaced: true,
  state: {
    // 
    classify: {
      statusGroup: [
        // 
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        }
      ],
      demotionGroup: [
        // 
        {
          value: 'force:return+null',
          label: ''
        },
        {
          value: 'fail:return+null',
          label: ''
        }
      ],
      loadGroup: [
        // 
        {
          value: 'random',
          label: ''
        },
        {
          value: 'roundrobin',
          label: ''
        },
        {
          value: 'leastactive',
          label: ''
        }
      ],
      priorityGroup: [
        // 
        {
          value: '0',
          label: '0'
        },
        {
          value: '1',
          label: '1'
        },
        {
          value: '2',
          label: '2'
        },
        {
          value: '3',
          label: '3'
        },
        {
          value: '4',
          label: '4'
        },
        {
          value: '5',
          label: '5'
        },
        {
          value: '6',
          label: '6'
        },
        {
          value: '7',
          label: '7'
        },
        {
          value: '8',
          label: '8'
        },
        {
          value: '9',
          label: '9'
        },
        {
          value: '10',
          label: '10'
        }
      ]
    },
    clusterOption: [], // 
    serviceList: [],
    serviceTotal: 0,
    consumerList: [],
    consumerTotal: 0
  },
  actions: {
    initializeData({state, commit}, {vm}) { // 
      return vm.$axios({
        method: 'get',
        url: Api.zkClusterOption
      }).then(res => {
        state.clusterOption = res.data && JSON.parse(res.data)
        return true
      }).catch(() => {
        return false
      })
    },
    // 
    serviceFind({
      state,
      commit
    }, data) {
      let vm = data[0]
      let value = data[1]
      let params = Object.assign({}, JSON.parse(JSON.stringify(value)))
      Object.keys(params).forEach(key => {
        if (params[key] === '' || params[key] === undefined || params[key] === null) {
          delete params[key]
        }
      })
      vm.$axios({
        method: 'post',
        url: Api.serviceList,
        data: params
      }).then(res => {
        // state.serviceList
        state.serviceTotal = res.data ? res.data.length : 0
        res.data.forEach((single) => {
          single.zkClusterId = value.zkClusterId
        })
        const stateData = {
          data: res.data,
          name: 'serviceList'
        }
        commit('paginationChange', stateData)
        vm.loading = false
        vm.$message({
          message: '',
          type: 'success'
        })
      }).catch(error => {
        vm.loading = false
        state.serviceList = []
        vm.currentPage = 1
        state.serviceTotal = 0
        vm.$message.error(error.data.message || error.message || error)
      })
    },
    // 
    findConsumer({
      state,
      commit
    }, data) {
      const vm = data[0]
      const values = data[1]
      let params = Object.assign({}, JSON.parse(JSON.stringify(values)))
      Object.keys(params).forEach(key => {
        if (params[key] === '' || params[key] === undefined || params[key] === null) {
          delete params[key]
        }
      })
      vm.$axios({
        method: 'post',
        url: Api.consumerList,
        data: params
      }).then(res => {
        state.consumerTotal = res.data ? res.data.length : 0
        const stateData = {
          data: res.data,
          name: 'consumerList'
        }
        commit('paginationChange', stateData)
        vm.loading = false
      }).catch(error => {
        vm.loading = false
        state.consumerList = []
        vm.currentPage = 1
        state.consumerTotal = 0
        vm.$message.error(error.data.message || error.message || error)
      })
    },
    // /
    consumerStatus({
      state
    }, data) {
      const vm = data[0]
      const params = data[1]
      let url = data[2] === 'enable' ? Api.consumerEnable : Api.consumerDisable
      vm.$axios({
        method: 'post',
        url,
        data: params
      }).then(res => {
        let news = data[2] === 'enable' ? '' : ''
        vm.$message({
          type: 'success',
          message: news
        })
        vm.search()
      }).catch(error => {
        vm.$message.error(error.data.message || error.message || error)
      })
    },
    // 
    serviceDemotion({
      state
    }, data) {
      const vm = data[0]
      const params = data[1]
      vm.$axios({
        method: 'post',
        url: Api.consumerDemotion,
        data: params
      }).then(res => {
        console.log(res)
        vm.$message({
          type: 'success',
          message: ''
        })
        vm.serviceCancel()
        vm.search()
      })
    },
    // 
    reDemotion({
      state
    }, data) {
      const vm = data[0]
      const params = data[1]
      vm.$axios({
        method: 'post',
        url: Api.consumerReDemotion,
        data: params
      }).then(res => {
        console.log(res)
        vm.$message({
          type: 'success',
          message: ''
        })
        vm.search()
      })
    }
  },
  mutations: {
    // 
    serviceFindTo(state, datas) {
      let vm = datas[0]
      let value = datas[1]
      vm.$axios({
        method: 'post',
        url: Api.serviceList,
        data: value
      }).then(res => {
        console.log(res)
        // state.serviceList
        state.serviceList = res.data
        vm.$message({
          message: '',
          type: 'success'
        })
      }).catch(error => {
        vm.$message.error(error.data.message || error.message || error)
      })
    },
    paginationChange(state, content = {}) {
      state[content.name] = []
      if (content.data.length > 0) {
        let num = Math.ceil(content.data.length / 10)
        let newArray = []
        for (let i = 0; i < num; i++) {
          let a = i * 10 + 0
          let b = i * 10 + 10
          newArray.push(content.data.slice(a, b))
        }
        state[content.name] = newArray
      }
    }
  }
}



