import moment from 'moment'

export default {
  pwdFormat(password) {
    const firstStr = password.charAt(0)
    const lastStr = password.charAt(password.length - 1)
    return firstStr + '****' + lastStr
  },
  dateSlice(dateTime) {
    return dateTime.substring(0, 10)
  },
  operationType(operation) {
    let res = ''
    switch (operation) {
      case 0:
        res = ''
        break
      case 1:
        res = ''
        break
      case 2:
        res = ''
        break
      case 3:
        res = ''
        break
      case 4:
        res = ''
        break
      case 5:
        res = '()'
        break
      case 6:
        res = '()'
        break
      case 7:
        res = ''
        break
      default:
        res = '()'
    }
    return res
  },
  changeType(type) {
    let changeType = ''
    switch (type) {
      case 'ADDED':
        changeType = ''
        break
      case 'MODIFIED':
        changeType = ''
        break
      case 'DELETED':
        changeType = ''
        break
      default:
    }
    return changeType
  },
  formatDateTime(time) {
    return moment(time).format('YYYY-MM-DD HH:mm:ss')
  },
  userStatus(status) {
    return status === 0 ? '' : ''
  },
  serStatus(status) {
    return status === 'true' ? '' : ''
  },
  mockStatus(data) {
    let text = ''
    let _data = parseInt(data)
    switch (_data) {
      case 0:
        text = ''
        break
      case 1:
        text = ''
        break
      case 2:
        text = ''
        break
      default:
    }
    return text
  },
  loadBalance(data) {
    let res = ''
    switch (data) {
      case 'random':
        res = ''
        break
      case 'roundrobin':
        res = ''
        break
      case 'leastactive':
        res = ''
        break
      default:
        res = ''
    }
    return res
  },
  // 
  hostMemory(data) {
    if (data) {
      return data + 'G'
    } else {
      return ''
    }
  },
  // 
  fileTypeMap(data) {
    return data === 'FULL' ? '' : ''
  },
  // text
  filterAppStatus(value) {
    let box = new Map([
      ['UNINSTALL', ''], //  
      ['INSTALL', ''],
      ['STOPPED', ''], // 
      ['STARTING', ''],
      ['RUNNING', ''], // 
      ['STOPPING', ''],
      ['EXITED', ''],
      ['FATAL', ''],
      ['UNKNOWN', ''],
      ['U_CREATED', ''],
      ['U_SUCCEED', ''],
      ['U_FAILURE', ''],
      ['U_SEC_FAILURE', ''],
      ['U_COMPLETED', '']
    ])
    return box.get(value) ? box.get(value) : ''
  },
  // color
  filterAppStatusColor(value) {
    let box = new Map([
      ['UNINSTALL', '#000000'],
      ['INSTALL', '#909399'],
      ['STOPPED', '#909399'],
      ['STARTING', '#67c23a'],
      ['RUNNING', '#67c23a'],
      ['STOPPING', '#909399'],
      ['EXITED', 'red'],
      ['FATAL', 'red'],
      ['UNKNOWN', 'red'],
      ['U_CREATED', '#000000'],
      ['U_SUCCEED', '#67c23a'],
      ['U_FAILURE', 'red'],
      ['U_SEC_FAILURE', 'red'],
      ['U_COMPLETED', '#67c23a']
    ])
    return {'color': box.get(value)}
  },
  instType(type) {
    let text = ''
    switch (parseInt(type)) {
      case 1:
        text = ''
        break
      case 2:
        text = ''
        break
      case 3:
        text = ''
        break
      default:
    }
    return text
  },
  instTypeStr(type) {
    let text = ''
    switch (type) {
      case '1':
        text = '()'
        break
      case '2':
        text = '()'
        break
      default:
        text = ''
    }
    return text
  },
  timeFilter(value, that) { // 
    let va = that.$moment(Math.trunc(value)).format('YYYY-MM-DD HH:mm:ss')
    return va
  },
  filterCommonMoType(valueData, moTypeData, type) {
    if (moTypeData && moTypeData.length > 0 && Array.isArray(moTypeData)) {
      let typeName = moTypeData.filter(item => item[type] === valueData)
      if (typeName.length > 0) {
        return typeName[0].name ? typeName[0].name : valueData
      } else {
        return valueData
      }
    } else {
      return valueData
    }
  },
  batchEnvCenter(value) {
    return value === 'A' ? '' : ''
  },
  filterEmpty(value) {
    let box = new Map([
      ['null', ''],
      ['undifined', ''],
      ['false', '']
    ])
    return box.get(value) || value
  }
}



