<template>
  <el-dialog title="" :visible.sync="$parent.centerVisible" width="30%" center>
    <el-form ref="centerForm" :model="centerForm" status-icon label-width="90px" size="small">
      <el-form-item label="">
        <el-row>
          <el-col :span="24">
            <el-input v-model="centerForm.name" type="text"></el-input>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item label="">
        <el-row>
          <el-col :span="24">
            <el-input v-model="centerForm.username" type="text"></el-input>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item label="">
        <el-row>
          <el-col :span="24">
            <el-input v-model="centerForm.email" type="text"></el-input>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item label="">
        <el-row>
          <el-col :span="24">
            <el-input v-model="centerForm.mobile" type="text"></el-input>
          </el-col>
        </el-row>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
                <el-button size="small" type="warning" @click="handleConfirm"> </el-button>
                <el-button size="small" @click="$parent.centerVisible = false"> </el-button>
            </span>
  </el-dialog>
</template>

<script>
  import {mapActions} from 'vuex'

  export default {
    data() {
      return {
        centerForm: {}
      }
    },
    created() {
      this.getPersonInfo(this)
    },
    methods: {
      ...mapActions('authority', ['getPersonInfo', 'editPersonal']),
      handleConfirm() {
        const vm = this
        if (!vm.regExpEmail(vm.centerForm.email)) {
          vm.$message({
            type: 'warning',
            message: ''
          })
          return
        }
        if (!vm.regExpPhone(vm.centerForm.mobile)) {
          vm.$message({
            type: 'warning',
            message: ''
          })
          return
        }
        vm.editPersonal(vm)
        vm.$parent.centerVisible = false
      }
    }
  }
</script>

<style type="text/scss" lang="scss" scoped>

</style>



